import React, { useReducer ,useState } from "react";
import { Button, Icon, TextField, Paper, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";  
import axios from "axios";
import { GET_ERRORS, SET_CURRENT_USER, USER_LOADING } from "../../actions/types";
import classnames from "classnames";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import jwt_decode from "jwt-decode";

 export default function UpdateFriend(props) {
   console.log(props.name)
  const history = useHistory();
  const [errors, seterrors] = useState({});
  const useStyles = makeStyles(theme => ({
    button: {
      margin: theme.spacing(1)
    },
    leftIcon: {
      marginRight: theme.spacing(1)
    },
    rightIcon: {
      marginLeft: theme.spacing(1)
    },
    iconSmall: {
      fontSize: 20
    },
    root: {
      padding: theme.spacing(3, 2)
    },
    container: {
      display: "flex",
      flexWrap: "wrap"
    },
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: "100%"
    }
  }));

  const [formInput, setFormInput] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    {
      name: props.name,
      addressUSDC: "",
      addressBUSD: "",
    }
  );

  const handleSubmit = evt  =>  {
    evt.preventDefault();

    let data = { formInput };
    

    // fetch("https://damp-bayou-54821.herokuapp.com/api/users/addaddress", {
    //   method: "POST",
    //   body: JSON.stringify(data.formInput),
    //   headers: {
    //     "Content-Type": "application/json"
    //   }
    // })
    //   // .then(res => history.push('/dashboard'))
    //   .then(response => console.log("Success:", response))
    //   .catch(error =>console.log(error));
    console.log("data.formInput",data.formInput)

      axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/updateFriend", data.formInput)
    .then(res => {
      console.log("login to dashboard")
      history.push('/login')
    })
    .catch(err =>
    //  console.log(err.response.data.name)
     seterrors(err.response.data)
    );
  };

  const handleInput = evt => {
    const name = evt.target.name;
    const newValue = evt.target.value;
   
    setFormInput({ [name]: newValue });
  };

  const classes = useStyles();

  console.log(props);

  return (
    <div>
      <Paper className={classes.root}>
        <Typography variant="h5" component="h3">
          {props.formName}
        </Typography>
        <Typography component="p">{props.formDescription}</Typography>

        <form onSubmit={handleSubmit}>
          <TextField
            label="UPDATE USDC Address"
            id="margin-normal"
            name="addressUSDC"
            defaultValue={formInput.addressUSDC}
            className={classes.textField}
            helperText={errors?.addressUSDC}
            onChange={handleInput}
          />
          <TextField
            label="UPDATE BUSD Address"
            id="margin-normal"
            name="addressBUSD"
            defaultValue={formInput.addressBUSD}
            className={classes.textField}
            helperText={errors?.addressBUSD}
            onChange={handleInput}
          />
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.button}
          >
            Add <Icon className={classes.rightIcon}>send</Icon>
          </Button>
        </form>
      </Paper>
    </div>
  );
}

