// Login - get user token
export const loginUser = userData => dispatch => {
  axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/login", userData)
    .then(res => {
      const { token } = res.data;
      localStorage.setItem("jwtToken", token);
      // Set token to Auth header
      setAuthToken(token);
      // Decode token to get user data
      const decoded = jwt_decode(token);
      // Set current user
      dispatch(setCurrentUser(decoded));

      // Save to localStorage

      // Set token to localStorage

    })
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};



//reset password for email verification
export const resetUser = (userData, history) => dispatch => {
  axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/reset", userData)
    .then(res => history.push("/"))
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};




// Set logged in user
export const setCurrentUser = decoded => {
  return {
    type: SET_CURRENT_USER,
    payload: decoded
  };
};

// User loading
export const setUserLoading = () => {
  return {
    type: USER_LOADING
  };
};

// Log user out
export const logoutUser = () => dispatch => {
  // Remove token from local storage
  const token = localStorage.jwtToken;
  // Decode token and get user info and exp
  const decoded = jwt_decode(token);
  if(decoded.name === "administrator"){
    axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/adminlogout", { name: decoded.name })
    .then(res => {
      localStorage.removeItem("jwtToken");
      // Remove auth header for future requests
      setAuthToken(false);
      // Set current user to empty object {} which will set isAuthenticated to false
      dispatch(setCurrentUser({}));
    })
    .catch(err =>
      console.log(err)
      //  seterrors(err.response.data)
    );
  }
  
  localStorage.removeItem("jwtToken");
  // Remove auth header for future requests
  setAuthToken(false);
  // Set current user to empty object {} which will set isAuthenticated to false
  dispatch(setCurrentUser({}));
};
