import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { loginUser } from "../../actions/authActions";
import classnames from "classnames";
import { StyledInput, StyledButton } from '../dashboard/config/containerStyles';
import BackLogo from '../dashboard/config/icons/backlogo.png';

class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: {}
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Login page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {

      if (this.state.email === 'admin20@gmail.com') {
        this.props.history.push("/setadmin");

      }
      else {
        console.log("dashboardnoadmin")
        this.props.history.push("/dashboard");
      }
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      if (this.state.email === 'admin20@gmail.com') {
        this.props.history.push("/setadmin");
      }
      else {
        console.log("dashboardnoadmin")
        this.props.history.push("/dashboard");
      }

    }

    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();

    const userData = {
      email: this.state.email,
      password: this.state.password
    };

    this.props.loginUser(userData);
  };

  render() {
    const { errors } = this.state;

    return (
      <div className="container fadein">
        <div style={{ marginTop: "4rem" }} className="row">
          <div className="col s8 offset-s2">
            <div className="col s12" style={{ paddingLeft: "11.250px" }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <Link to="/" className="btn-flat waves-effect" style={{ margin: '0', padding: '0' }}>
                  <img src={BackLogo} alt="Back" />
                </Link>
                <div style={{
                  fontFamily: 'Roboto,sans-serif',
                  fontWeight: '800',
                  fontSize: '26px',
                  color: '#000000ba'
                }}
                >
                  Sign in to continue
                </div>
              </div>
              <p className="grey-text text-darken-1">
                Don't have an account? <Link to="/register">Register</Link>
              </p>
            </div>
            <form noValidate onSubmit={this.onSubmit}>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Email"
                  onChange={this.onChange}
                  value={this.state.email}
                  error={errors.email}
                  id="email"
                  type="email"
                  className={classnames("", {
                    invalid: errors.email || errors.emailnotfound
                  })}
                />
                <span className="red-text">
                  {errors.email}
                  {errors.emailnotfound}
                </span>
              </div>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Password"
                  onChange={this.onChange}
                  value={this.state.password}
                  error={errors.password}
                  id="password"
                  type="password"
                  className={classnames("", {
                    invalid: errors.password || errors.passwordincorrect
                  })}
                />
                <span className="red-text">
                  {errors.password}
                  {errors.passwordincorrect}
                </span>
              </div>
              <div className="col s12" style={{ paddingLeft: "11.250px", display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                <StyledButton type="submit">
                  Login
                </StyledButton>
              </div>
            </form>
            <div style={{textAlign: 'right', marginRight: '24px'}}>
              <Link to="/forget">Forget password</Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Login.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { loginUser }
)(Login);
