import React, { Component } from 'react'
// import { notify } from 'react-notify-toast'
import Spinner from './Spinner'
import { API_URL } from '../../config'
import transitions from '@material-ui/core/styles/transitions'
import { Link } from "react-router-dom";
import axios from "axios";
import { StyledInput, StyledButton } from '../dashboard/config/containerStyles';
import BackLogo from '../dashboard/config/icons/backlogo.png';

export default class Job extends Component {

  // A bit of state to give the user feedback while their nickname address is being 
  // added to the User model on the server.
  state = {
    sendingjob: false
  }

  onSubmit = event => {
    event.preventDefault()
    this.setState({ sendingjob: true })
    
    axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/jobcheck", {job: this.job.value})
    .then(res => {
      this.setState({ sendingjob: false })
        // notify.show(data.msg)
        this.form.reset()
      this.props.history.push("/dashboard");
    })
    .catch(err =>{
      console.log(err);
      this.setState({ sendingjob: false })
      alert(err.response.data)
    }
      
    );
    // Super interesting to me that you can mess with the upper and lower case 
    // of the headers on the fetch call and the world does not explode.
    // fetch(`${API_URL}/nickname`, {
    //   method: 'pOSt',
    //   headers: {
    //     aCcePt: 'aPpliCaTIon/JsOn',
    //     'cOntENt-type': 'applicAtion/JSoN'
    //   },
    //   body: JSON.stringify({ nickname: this.nickname.value })
    // })
    //   .then(res => res.json())
    //   .then(data => {

    //     // Everything has come back successfully, time to update the state to 
    //     // reenable the button and stop the <Spinner>. Also, show a toast with a 
    //     // message from the server to give the user feedback and reset the form 
    //     // so the user can start over if she chooses.
    //     this.setState({ sendingnickname: false })
    //     // notify.show(data.msg)
    //     alert(data.msg);
    //     this.form.reset()
    //   })
    //   .catch(err => console.log(err))
  }

  onLogout() {
    
    axios
    .post("https://damp-bayou-54821.herokuapp.com/api/users/adminlogout", { name: "administrator" })
    .then(res => {
      localStorage.clear();
      window.location.href = '/';
    })
    .catch(err =>
      console.log(err)
      //  seterrors(err.response.data)
    );
  }

  render = () => {

    // This bit of state provides user feedback in the component when something
    // changes. sendingnickname is flipped just before the fetch request is sent in 
    // onSubmit and then flipped back when data has been received from the server.
    // How many times is the 'sendingnickname' variable used below?
    const { sendingjob } = this.state

    return (

      <div style={{display: "flex",justifyContent: "center", flexDirection: "column", height: "100vh"}} className='fadein'>
        {/* 
          <Notifications > component from 'react-notify-toast'. This is the 
          placeholder on the dom that will hold all the feedback toast messages 
          whenever notify.show('My Message!') is called.
        */}
        {/* <Notifications /> */}
       
        <div style={{display: "flex",alignItems:"center",justifyContent: "center",flexDirection:"column"}}>
        <div className="col s8 offset-s2 nicknav" >
            {/* <Link to="/nickname" className="btn-flat waves-effect">
              <i className="material-icons left">keyboard_backspace</i> Back to
              Login
            </Link> */}
            <div className="col s12" style={{ paddingLeft: "11.250px" }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <Link to="/" className="btn-flat waves-effect" style={{ margin: '0', padding: '0' }}>
                  <img src={BackLogo} alt="Back" />
                </Link>
                <div style={{
                  fontFamily: 'Roboto,sans-serif',
                  fontWeight: '800',
                  fontSize: '26px',
                  color: '#000000ba'
                }}
                >
                  Set Job
                </div>
              </div>
              <p className="grey-text text-darken-1">
                Don't have an account? <Link to="/register">Register</Link>
              </p>
            </div>
          </div>
          <form
            style={{ position: "relative", display: "flex" }}
            onSubmit={this.onSubmit}
            ref={form => this.form = form}
          >
            <div>
              <StyledInput
                placeholder='Job'
                className="nickinput"
                type='text'
                name='job'
                ref={input => this.job = input}
                required
              />
              {/* 
            Putting the label after the input allows for that neat transition
            effect on the label when the input is focused.
          */}
            </div>
            <div>
              {/* 
            While the nickname is being sent from the server, provide feedback that
            something is happening by disabling the button and showing a 
            <Spinner /> inside the button with a smaller 'size' prop passed in.
          */}
              <StyledButton type='submit' style={{ marginLeft: '6px' }} disabled={sendingjob}>
                {sendingjob
                  ? <Spinner size='lg' spinning='spinning' />
                  : "Continue"
                }
              </StyledButton>
            </div>
          </form>
          <button
                  style={{
                    width: "150px",
                    borderRadius: "3px",
                    letterSpacing: "1.5px",
                    marginTop: "1rem",
                    position: "absolute",
                    right: "10px",
                    bottom: "10px"
                  }}
                  onClick={this.onLogout}
                  className="btn btn-large waves-effect waves-light hoverable blue accent-3"
                >Logout</button>
        </div>
        
      </div>
      

    )
  }
}