import React, { useReducer, useState,useEffect } from "react";
import { Button, Icon, TextField, Paper, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { GET_ERRORS, SET_CURRENT_USER, USER_LOADING } from "../../actions/types";
import classnames from "classnames";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import jwt_decode from "jwt-decode";
import Loading from "./Loading";
import ErrorMessage from "./ErrorMessage";
// const adminaddress = '0xe4F0092135280A171f66CEE1cE81F1a6Ef38dF28'
import busd_abi from "./BUSD_abi.json";
import { ethers } from "ethers";
import ethersGasUtils from "./ethersGasUtils";

const busd_address = `0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56`;

export default function Addcharge(props) {
  const history = useHistory();
  const [error, setError] = useState();
  const [errors, seterrors] = useState({});
  const [adminaddress, setadminaddress] = useState('')
  const [loading, setLoading] = useState(false);
  const useStyles = makeStyles(theme => ({
    button: {
      margin: theme.spacing(1)
    },
    leftIcon: {
      marginRight: theme.spacing(1)
    },
    rightIcon: {
      marginLeft: theme.spacing(1)
    },
    iconSmall: {
      fontSize: 20
    },
    root: {
      padding: theme.spacing(3, 2)
    },
    container: {
      display: "flex",
      flexWrap: "wrap"
    },
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: "100%"
    }
  }));


useEffect(() => {
  axios
        .post("https://damp-bayou-54821.herokuapp.com/api/users/adminaddress", {name:'administrator'})
        .then(res => {
          console.log("adminaddress",res.data.address)
          setadminaddress(res.data.address)
        })
        .catch(err =>
          console.log(err)
          //  seterrors(err.response.data)
        );
}, []);




  const [formInput, setFormInput] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    {
      chargeAmount: ""
    }
  );
  const chains = [
    {
      chainId: "0x3",
      rpcUrls: ["https://ropsten.infura.io/v3/"],
      chainName: "Ropsten Testnet",
      blockExplorerUrls: ["https://ropsten.etherscan.io"]


    },
    {
      chainId: "0x38",
      rpcUrls: ["https://bsc-dataseed1.binance.org"],
      chainName: "Binance",
      nativeCurrency: {
        name: "BNB",
        symbol: "BNB",
        decimals: 18
      },
      blockExplorerUrls: ["https://bscscan.com"]
    },
  ]

  const handleSubmit = async (evt) => {
    if(adminaddress){
   
    evt.preventDefault();
    await window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: `0x38` }],
    }).then(() => console.log("switch success"))
    // let data = { formInput };
    console.log(formInput.chargeAmount)
    setError();
    await startPayment({
      setError,
      ether: formInput.chargeAmount,
      addr: adminaddress,
    });
  }



  };
  const startPayment = async ({ setError, ether, addr }) => {

    try {
      
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();

      let contract = new ethers.Contract(
        busd_address,
        busd_abi,
        signer
      )

      // How many tokens?
      let numberOfTokens = ethers.utils.parseUnits(ether, 18)
      console.log(`numberOfTokens: ${numberOfTokens}`)
      
      const {getGasPrice,getGasLimit} = await ethersGasUtils(provider,contract,[addr,numberOfTokens]);
      // Send tokens
      const sendNativeToken = await contract.transfer(addr, numberOfTokens).then((transferResult) => {
        console.dir(transferResult);

        // setAmount(ether);
        // setToken("BUSD");
        // setTxs([transferResult]);


      });
      console.log("NativeTOken", sendNativeToken)
      setLoading(true);
      await sendNativeToken;
      setLoading(false);
      if (localStorage.jwtToken) {

        // Set auth token header auth
        const token = localStorage.jwtToken;
        // Decode token and get user info and exp
        const decoded = jwt_decode(token);
        console.log(decoded.name)
  
        if(decoded.name){
          axios
        .post("https://damp-bayou-54821.herokuapp.com/api/users/recharge", {user:decoded.name, amount:ether})
        .then(res => history.push("/login"))
        .catch(err =>
          console.log(err)
          //  seterrors(err.response.data)
        );

        }
      }

      


    } catch (err) {
      console.log('err', err)
      if (err.data !== undefined && err.data.message !== undefined) setError(err.data.message);
      else setError(err.message);
    }
  };



  const handleInput = evt => {
    const name = evt.target.name;
    const newValue = evt.target.value;

    setFormInput({ [name]: newValue });
  };

  const classes = useStyles();

  console.log(props);

  return (

    <div>
      {loading ? <Loading /> : <></>}
      <ErrorMessage message={error} />
      <Paper className={classes.root}>
        <Typography variant="h5" component="h3">
          {props.formName}
        </Typography>
        <Typography component="p">{props.formDescription}</Typography>

        <form onSubmit={handleSubmit}>
          {/* <TextField
            label="Name"
            id="margin-normal"
            name="name"
            defaultValue={formInput.name}
            className={classes.textField}
            // className={classnames('', {
            //   invalid: errors?.name
            // })}
            helperText={errors?.name}
            onChange={handleInput}
          /> */}
          <TextField
            label="chargeAmount"
            id="margin-normal"
            name="chargeAmount"
            defaultValue={formInput.chargeAmount}
            className={classes.textField}
            helperText={errors?.chargeAmount}
            onChange={handleInput}
          />
          {/* <TextField
            label="BNB Address"
            id="margin-normal"
            name="addressBNB"
            defaultValue={formInput.addressBNB}
            className={classes.textField}
            helperText={errors?.addressBNB}
            onChange={handleInput}
          />
          <TextField
            label="BUSD Address"
            id="margin-normal"
            name="addressBUSD"
            defaultValue={formInput.addressBUSD}
            className={classes.textField}
            helperText={errors?.addressBUSD}
            onChange={handleInput}
          /> */}
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.button}
          >
            Add <Icon className={classes.rightIcon}>send</Icon>
          </Button>
        </form>
      </Paper>
    </div>
  );
}

