const ethersGasUtils = async (provider, contract, params) => {
    const getGasPrice = await provider.getGasPrice();
    const getGasLimit = await contract.estimateGas.transfer(params[0], params[1]);
    return ({
        getGasPrice: getGasPrice,
        getGasLimit: getGasLimit,
        etherOptions: {
            getGasPrice,
            getGasLimit
        }
    })
}

export default ethersGasUtils;