import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { setAdmin } from "../../actions/authActions";
import classnames from "classnames";
import axios from "axios";
import jwt_decode from "jwt-decode";
import { StyledInput, StyledButton } from '../dashboard/config/containerStyles';
import BackLogo from '../dashboard/config/icons/backlogo.png';

class SetAdmin extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      address: "",
      nickname: "",
      job: "",
      errors: {}
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Register page, should redirect them to dashboard
    if (localStorage.jwtToken) {

      // Set auth token header auth
      const token = localStorage.jwtToken;
      // Decode token and get user info and exp
      const decoded = jwt_decode(token);
      if (decoded.name === 'administrator') {
        axios
          .post("https://damp-bayou-54821.herokuapp.com/api/users/admincheck", { name: decoded.name })
          .then(res => this.props.history.push("./nickname"))
          .catch(err =>
            console.log(err)
            //  seterrors(err.response.data)
          );
      } else {
        this.props.history.push("./dashboard")
      }

    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    //console.log("Rigister Tapped");
    e.preventDefault();

    const admininfo = {
      name: this.state.name,
      address: this.state.address,
      nickname: this.state.nickname,
      fee: 0,
      job: this.state.job
    };

    this.props.setAdmin(admininfo, this.props.history);
  };

  onLogout() {

    axios
      .post("https://damp-bayou-54821.herokuapp.com/api/users/adminlogout", { name: "administrator" })
      .then(res => {
        localStorage.clear();
        window.location.href = '/';
      })
      .catch(err =>
        console.log(err)
        //  seterrors(err.response.data)
      );
  }

  render() {
    const { errors } = this.state;

    return (
      <div className="container fadein">
        <div className="row">
          <div className="col s8 offset-s2">
            <div className="col s12" style={{ paddingLeft: "11.250px",marginTop:'10px' }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <Link to="/" className="btn-flat waves-effect" style={{ margin: '0', padding: '0' }}>
                  <img src={BackLogo} alt="Back" />
                </Link>
                <div style={{
                  fontFamily: 'Roboto,sans-serif',
                  fontWeight: '800',
                  fontSize: '26px',
                  color: '#000000ba'
                }}
                >
                  Create an account
                </div>
              </div>
              <p className="grey-text text-darken-1">
                Already have an account? <Link to="/login">Log in</Link>
              </p>
            </div>
            <form noValidate onSubmit={this.onSubmit}>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Name"
                  onChange={this.onChange}
                  value={this.state.name}
                  error={errors.name}
                  id="name"
                  type="text"
                  className={classnames("", {
                    invalid: errors.name
                  })}
                />
                <span className="red-text">{errors.name}</span>
              </div>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Address"
                  onChange={this.onChange}
                  value={this.state.address}
                  error={errors.address}
                  id="address"
                  type="text"
                  className={classnames("", {
                    invalid: errors.address
                  })}
                />
                <span className="red-text">{errors.address}</span>
              </div>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Nick Name"
                  onChange={this.onChange}
                  value={this.state.nickname}
                  error={errors.nickname}
                  id="nickname"
                  type="text"
                  className={classnames("", {
                    invalid: errors.nickname
                  })}
                />
                <span className="red-text">{errors.nickname}</span>
              </div>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Admin Job"
                  onChange={this.onChange}
                  value={this.state.job}
                  error={errors.job}
                  id="job"
                  type="text"
                  className={classnames("", {
                    invalid: errors.job
                  })}
                />
                <span className="red-text">{errors.job}</span>
              </div>
              <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                <StyledButton type="submit">
                  Set Admin
                </StyledButton>
              </div>
            </form>
            <button
              style={{
                width: "150px",
                borderRadius: "3px",
                letterSpacing: "1.5px",
                marginTop: "1rem",
                position: "absolute",
                right: "10px",
                bottom: "10px"
              }}
              onClick={this.onLogout}
              className="btn btn-large waves-effect waves-light hoverable blue accent-3"
            >Logout</button>
          </div>
        </div>
      </div>
    );
  }
}

SetAdmin.propTypes = {
  setAdmin: PropTypes.func.isRequired,

  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { setAdmin }
)(withRouter(SetAdmin));
