import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { resetUser } from "../../actions/authActions";
import classnames from "classnames";
import { API_URL } from '../../config'
import { StyledInput, StyledButton } from '../dashboard/config/containerStyles';
import BackLogo from '../dashboard/config/icons/backlogo.png';

class Resetpassword extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      password2: "",
      errors: {}
    };
  }

  componentDidMount = () => {
    const { id } = this.props.match.params

    fetch(`${API_URL}/email/confirm/${id}`)
      .then(res => res.json())
      .then(data => {
        console.log("reset", data)
        this.setState({ useremail: data.userEmail });
        this.setState({ confirming: false })
        if (data.userConfirm === true) {
          this.props.history.push("/");
        }
        alert(data.msg)
      })
      .catch(err => console.log(err))
  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    //console.log("Rigister Tapped");
    e.preventDefault();

    const resetUser = {
      email: this.state.useremail,
      password: this.state.password,
      password2: this.state.password2
    };

    this.props.resetUser(resetUser, this.props.history);
  };

  render() {
    const { errors } = this.state;

    return (
      <div className="container">
        <div className="row">
          <div className="col s8 offset-s2">
            <div style={{ display: 'flex', alignItems: 'center', gap: '4px',marginTop:'10px' }}>
              <Link to="/" className="btn-flat waves-effect" style={{ margin: '0', padding: '0' }}>
                <img src={BackLogo} alt="Back" />
              </Link>
              <div style={{
                fontFamily: 'Roboto,sans-serif',
                fontWeight: '800',
                fontSize: '26px',
                color: '#000000ba'
              }}
              >
                Reset your password
              </div>
            </div>
            <p className="grey-text text-darken-1">
              Already have an account? <Link to="/login">Log in</Link>
            </p>
            <form noValidate onSubmit={this.onSubmit}>

              <div className="input-field col s12">
                <StyledInput
                  placeholder="Email"
                  onChange={this.onChange}
                  value={this.state.useremail}
                  error={errors.email}
                  id="email"
                  type="email"
                  className={classnames("", {
                    invalid: errors.email || errors.emailnotfound
                  })}
                />

              </div>



              <div className="input-field col s12">
                <StyledInput
                  placeholder="Password"
                  onChange={this.onChange}
                  value={this.state.password}
                  error={errors.password}
                  id="password"
                  type="password"
                  className={classnames("", {
                    invalid: errors.password
                  })}
                />
                <span className="red-text">{errors.password}</span>
              </div>
              <div className="input-field col s12">
                <StyledInput
                  placeholder="Confirm Password"
                  onChange={this.onChange}
                  value={this.state.password2}
                  error={errors.password2}
                  id="password2"
                  type="password"
                  className={classnames("", {
                    invalid: errors.password2
                  })}
                />
                <span className="red-text">{errors.password2}</span>
              </div>
              <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                <StyledButton type="submit">
                  Reset Password
                </StyledButton>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

Resetpassword.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { resetUser }
)(withRouter(Resetpassword));
