import React from 'react'
import { Oval } from 'react-loading-icons'

const Loading = () => {
  return (
    <div className='loading_panel'>
      <div class="loader">
      <p style={{
        marginTop: "104%",
        width: "350px",
        marginLeft: "-20%"
      }}>
        Please wait transaction is getting completed.<br/>
        If you reload and exit page, you will lost transaction.
        </p>
        <div class="inner one"></div>
        <div class="inner two"></div>
        <div class="inner three"></div>
       
      </div>

    </div>
  )
}

export default Loading;
