import styled from "styled-components";

export const StyledContainer = styled.div`
    font-family: 'Roboto', sans-serif;
    z-index: 1;
  `;

export const StyledNav = styled.div`
    display: flex;
    flex-direction: row;
    height: 66px;
    background: #142942;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;

    @media (max-width: 700px) {
      justify-content: center;
    }
  `;

export const StyledTitle = styled.div`
    display: flex;
    flex-direction: row;
    width: 50%;
    gap: 8px;
    justify-content: flex-end;
    align-items: center;
    color: white;
    font-size: 32px;
    font-weight: 500;
    margin-right: 10px;
    user-select: none;

    @media (max-width: 700px) {
      width: auto;
    }
  `;

export const Fader = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin: 100px 0px 100px 0px;
  `;

export const LogoutContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: #6396c0;
    padding: 5px 10px;
    border-radius: 10px;
    font-weight: 500;
    user-select: none;
    font-size: 16px;
    border: none;
    cursor: pointer;

    &&&:hover,:active,:focus {
      background: #6396c0ad;
    }
  `;

export const LogoutButton = styled.button`
    background: none;
    font-weight: 600;
    user-select: none;
    font-size: 16px;
    border: none;
    color: white;
    cursor: pointer;
  `;

export const ConnectButton = styled.div`
    background: #0057ff;
    color: white;
    padding: 10px 20px;
    border-radius: 20px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    font-size: 16px;
    border: none;

    &&&:hover,:active,:focus {
      background: #003ECB;
    }
  `;

export const PaymentContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 16px;
    z-index: 1;
  `;

export const QRPaymentContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: #5198ef;
    padding: 5px 10px;
    border-radius: 8px;
    user-select: none;

    &&&:hover,:active,:focus {
      background: #5198efb8;
    }
  `;

export const RechargeContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: #2896d7;
    padding: 5px 10px;
    border-radius: 8px;
    user-select: none;
    
    &&&:hover,:active,:focus {
      background: #2896d7ad;
    }
  `;

export const RechargeButton = styled.button`
    background: none;
    border: none;
    cursor: pointer;
    color: white;
    font-weight: 500;
  `;
export const QRPaymentButton = styled.button`
    background: none;
    border: none;
    cursor: pointer;
    color: white;
    font-weight: 500;
  `;

export const FriendContainer = styled.div`
    z-index: 1;
    height: 100px;
    width: 500px;
    max-width: 80vw;
    display: flex;
    flex-direction: column;
    gap: 8px;
  `;

export const AdminFriendContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  `;

export const FriendButton = styled.button`
    padding: 10px 20px;
    border-radius: 16px;
    background: #0d1b78;
    color: white;
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
    width: ${props => props.Width};
    max-width: 80vw;
    border: none;
    user-select: none;

    &&&:hover,:active,:focus {
      background: #0e1962;
    }
  `;


export const QRCard = styled.div`
    width: 500px;
    max-width: 80vw;
    z-index: 1;
  `;

export const QRInput = styled.input`
    &&& {
    text-align: center;
    color: white;
    font-weight: 600;
    background: white;
    border-radius: 10px;
    }
  `;

export const AdminLabel = styled.div`
    font-weight: 200;
    font-size: 14px;
    text-align: right;
    color: #ffffff82;
  `;

export const TransferCard = styled.div`
    width: 500px;
    max-width: 80vw;
    background: #0a263e;
    z-index: 1;
    border-radius: 10px;
    color: white;
    box-shadow: 0px 0px 9px 1px #0000ff91;
  `;

export const TransferCardTitle = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 4px;
    font-size: 32px;
  `;

export const TransferForm = styled.form`
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 16px;
  `;

export const TransferInput = styled.input`
    text-align: center;
    color: white;
    font-weight: 600;

    &&&:focus {
      color: white;
    }
  `;

export const TransferButton = styled.button`
    padding: 10px 20px;
    border-radius: 16px;
    background: #18d5c8;
    color: white;
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
    border: none;
    user-select: none;

    &&&:hover,:active,:focus {
      background: #18d5c8d1;
    }

    &&&:disabled {
        background: #b6cee3;
        cursor: auto;
    }
  `;

export const StyledInput = styled.input`
  &&&{
    border-radius: 16px !important;
    border: 1px solid #80808030 !important;
  }

  &&&:focus {
    border-bottom: 1px solid blue !important;
    border-top: 1px solid blue !important;
    border-left: 1px solid blue !important;
    border-right: 1px solid blue !important;
    -webkit-box-shadow: 0 0 0 1px blue !important;
    box-shadow: 0 0 0 1px blue !important;
  }

  &&&::placeholder {
    padding-left: 10px;
  }

  &&&.invalid {
    border-bottom: 1px solid red !important;
    border-top: 1px solid red !important;
    border-left: 1px solid red !important;
    border-right: 1px solid red !important;
    -webkit-box-shadow: 0 0 0 1px red !important;
    box-shadow: 0 0 0 1px red !important;
  }
`;

export const StyledButton = styled.button`
    padding: 10px 20px;
    color: white;
    background: blue;
    border: none;
    width: 100%;
    height: 50px;
    border-radius: 20px;
    cursor: pointer;
    outline: none;

    &&&:hover,:active,:focus {
      background: #1616e7;
    }
`;

export const TxnsContainer = styled.div`
    width: 500px;
    height: 247px;
    font-family: monospace;
    overflow-y: scroll;
    max-width: 80vw;
    background: #0a263e;
    z-index: 1;
    border-radius: 10px;
    color: white;
    box-shadow: 0px 0px 9px 1px #ff00c291;
  `;

export const TxnsCard = styled.div`
  padding: 4px;
  border-bottom: 1px solid #00ffc2bd;
`;

export const TxnsTitle = styled.div`
  font-family: Roboto, sans-serif;
  font-weight: 800;
  font-size: 20px;
  text-align: center;
`;

export const CSSLoading = styled.div`
  z-index: 10;
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);

  &&&::before {
    width: 50%;
    height: 50%;
    background: aliceblue;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
  }

  &&&::after {
    background: #122841;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

    @-webkit-keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
    }

    @keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
    }
`;