import React, { useEffect, useState } from 'react';
// import logo from './logo.svg';
// import './App.css';
import { useHtml5QrCodeScanner, useAvailableDevices } from 'react-html5-qrcode-reader';

const html5QrCodeScannerFile = process.env.PUBLIC_URL + '/html5-qrcode.min.js'; // <-- this file is in /public.

// {"friend":"0xe4F0092135280A171f66CEE1cE81F1a6Ef38dF28","currency":"BUSD","amount":"1"}

function QRpayment(props) {
  const [data, setData] = useState('');
  const { Html5QrcodeScanner } = useHtml5QrCodeScanner(
    html5QrCodeScannerFile
  );
  const { devices, error } = useAvailableDevices(
    html5QrCodeScannerFile
  );

  useEffect(() => {
    if (data) {
      console.log("child component QR data---------", data)
      // console.log(objectData)
      props.sendToParent(data);
    }
  }, [data, props]);

  useEffect(() => {
    if (Html5QrcodeScanner) {
      let html5QrcodeScanner = new Html5QrcodeScanner(
        "reader",
        { fps: 60, qrbox: { width: 250, height: 250 } },
        /* verbose= */ false);
      html5QrcodeScanner.render(
        // (data) => setData(data),
        // ANC data=qrCodeData-->sendToDashboard => setQrdata(state_changes)
        // ANC if(qrData)=>useEffect(...) perform JSON.parse on a string??
        (data) => setData(data),
        (err) => console.log('err ->', err)
      );
    }
  }, [Html5QrcodeScanner]);

  return (
    <div id='reader' className="qrcode"></div>
  );
}

export default QRpayment;
