import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import styled from "styled-components";
import videoSegment from './video/videosegment.mp4';
import playIcon from './icons/play.png';
import Logo from './icons/logo.png';

const StyledVidContainer = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: calc(100vw - (100vw - 100%));
    height: 100vh;
`;


const StyledVideo = styled.video`
  min-width: 100%; 
  min-height: 100%;
`;

const VideoOverlay = styled.div`
    position: absolute;
    bottom: 160px;
    left: 20px;
    width: calc(100vw - (100vw - 100%));
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 32px;
    flex-wrap: wrap;
`;

const OverlayButton = styled.button`
    font-family: Roboto;
    font-size: 64px;
    font-weight: 400;
    background: none;
    color: white;
    border: 2px solid white;
    padding: 10px 20px;
    cursor: pointer;

    &:hover {
        box-shadow: 0px 0px 20px 0px white;
        transform: scale(1.01);
    }

    @media (max-width: 720px) {
        &&&{margin-top: 0px !important;}
    }
`;

const LearnButton = styled.button`
    font-family: Roboto;
    font-size: 64px;
    font-weight: 400;
    background: none;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
`;

const VideoComponent = ({ auth }) => {
    const [flag, setFlag] = useState(false);

    // const history = useHistory();
    useEffect(() => {
        if (auth.isAuthenticated) {
            // history.push('/dashboard');
            setFlag(true);
        } else
            setFlag(false);

    }, []);

    return (
        <StyledVidContainer>
            <Link
                to="/"
                style={{
                    fontFamily: "monospace"
                }}
                className="col s5 brand-logo center black-text"
            >
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: '4px',height:'70px' }}>
                    <img src={Logo} alt="logo" width="50px" height="50px" />
                    <div style={{ fontFamily: 'Roboto, sans-serif', fontWeight: '800', color: 'black',fontSize:'32px' }}>TEG Finance</div>
                </div>
            </Link>
            <StyledVideo autoPlay muted loop>
                <source src={videoSegment} type="video/mp4" />
            </StyledVideo>
            <VideoOverlay>
                <Link to="/register">
                    <OverlayButton>Register</OverlayButton>
                </Link>
                <a href='https://youtu.be/KbC8FHuWgpg' target='_blank' rel="noreferrer noopener" style={{textDecoration:'none',color:'white'}}>
                    <OverlayButton style={{ marginTop: '240px'}}>Learn More
                        <img src={playIcon} alt="Play" />
                    </OverlayButton>
                </a>
                {
                    flag === true ? <Link to="/dashboard">
                        <OverlayButton>Login</OverlayButton>
                    </Link> : <Link to="/login">
                        <OverlayButton>Login</OverlayButton>
                    </Link>
                }
            </VideoOverlay>
        </StyledVidContainer>
    )
}

const mapStateToProps = state => ({
    auth: state.auth
});

export default connect(mapStateToProps)(VideoComponent);