import React, { Component } from "react";
import { Link, useLocation } from "react-router-dom";
import Logo from '../dashboard/config/icons/logo.png';

const Navbar = () => {
  const location = useLocation();
  let Nav = () => <div className="navbar-fixed">
    <nav className="z-depth-0">
      <div className="nav-wrapper backcolor">
        {/* style={{background:"#162a43"}} */}
        <Link
          to="/"
          style={{
            fontFamily: "monospace"
          }}
          className="col s5 brand-logo center black-text"
        >
        <div style={{display: 'flex',justifyContent:'center',alignItems: 'center',gap:'4px'}}>
            <img src={Logo} alt="logo" width="50px" height="50px" />
            <div style={{ fontFamily: 'Roboto, sans-serif',fontWeight:'800',color: 'white' }}>TEG Finance</div>
        </div>
        </Link>
      </div>
    </nav>
  </div>;

  return (
    <div>
      {location.pathname !== '/dashboard' && location.pathname !== '/' ? <Nav /> : null}
    </div>
  )
}

export default Navbar;
